  .load-circle {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center; }
    .load-circle .circle-2 {
      width: 120px;
      height: 120px;
      position: absolute;
      border: 2px solid var(--rgba-primary-4);
      opacity: 1;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center; }
      .load-circle .circle-2 img {
        max-width: 55px; }
      .load-circle .circle-2::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        border: 2px solid var(--primary);
        border-radius: 50%;
        top: 50%;
        left: 50%;
        z-index: -1;
        transform: translate(-50%, -50%);
        animation: load-circle 1s ease-in-out infinite; }
      .load-circle .circle-2::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        border: 2px solid var(--primary);
        border-radius: 50%;
        top: 50%;
        left: 50%;
        z-index: -1;
        transform: translate(-50%, -50%);
        animation: load-circle2 1s ease-in-out infinite; }
  
